
import { defineComponent } from "vue";
import Methods from "@/api/methods";

export default defineComponent({
  name: "LogIn",
  data() {
    return {
      user: {},
    };
  },
  methods: {
    async login() {
      let response = await Methods.doLogin(
        this.user.userId,
        this.user.password
      );
      console.log(response);
    },
  },
});
